.banner-back {
    position: relative;
    display: flex;
    width: 100%;
    max-height:350px;
    height: 34%;
}

.banner-img-cont {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: black;
    width: 100%;
    overflow: hidden;
}

.banner-img {
    width: auto;
    height: auto;
    max-width: 100vw;
    max-height: 500px;
}

.banner-temp {
    position: relative;
    width:100%;
    max-height:350px;
    height:34%;
    background-color:black;
    overflow:hidden;
}

.banner-img-temp {
    height:350px;
    max-height:500px;
}