.cont-cont {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
    padding: 30px;
    gap: 50px;

    background-image: linear-gradient(black,whitesmoke);
}

.topic-cont {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    margin: auto;
    gap:30px;
    padding: 30px;

    border: 2px solid black;
    border-radius: 20px;

    background-color: whitesmoke;
}

.topic {
    width: 300px;
    max-width: 100vw;

    height: fit-content;

    border: 2px solid black;
    border-radius: 10px;

    padding: 20px;
    padding-top: 0px;

    background-color: white;
}

.lnk {
    display: inline-block;
    position: relative;
    color: black;
    text-decoration: none;
    transition: all .3s ease;
    -webkit-transition: all .3s ease;
}

.lnk:hover {
    color: #738290;
}

.lnk:after {
    content: 'âž¤';
    color: #738290;
    position: absolute;
    opacity: 0;
    right: -25px;
    transition: 0.3s;
}

.lnk:hover {
    padding-right:28px;
}

.lnk:hover:after {
    opacity: 1;
    right: 10px;
}