.search-bar-cont {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.search-wrap {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    width: 80vw;
    max-width: 800px;
    height: 50px;

    background-color: white;
    border-radius: 25px;
    margin-bottom: 10px;
}

.search-input {
    background-color: transparent;
    border: none;
    
    font-family: "Funnel Display", sans-serif; 
    font-weight: 300;
    font-size: 1rem;

    height: 45px;
    width: 70vw;
    max-width: 700px;

    margin-left: calc(min(10%,30px));
    margin-right: calc(min(10%,30px));
}

.search-input:focus {
    outline: none;
}

.search-list {
    display: flex;
    flex-direction: column;

    height:fit-content;
    max-height: 300px;
    width: 70vw;
    max-width: 700px;

    background-color: white;
    border-radius: 10px;
    overflow-y: scroll;
}

.result-sect {
    font-family: "Funnel Display", sans-serif; 
    font-weight: 300;
    font-size: 0.9rem;

    margin: 10px;
}

.result-lnk {
    text-decoration: none;
    color: black;
    transition: all .3s ease;
    -webkit-transition: all .3s ease;
}

.result-lnk:hover {
    color: #738290;
}