.page-back {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
    padding: 30px;
    gap: 50px;

    background-image: linear-gradient(black,whitesmoke);
}

.topic-content-temp {
    display: flex;
    justify-content: center;
    align-items: center;

    flex-wrap: wrap;
    flex-direction: column;

    width: 80vw;
    max-width: 1000px;

    margin: auto;
    gap: 30px;
    padding: 30px;

    border: 2px solid black;
    border-radius: 20px;

    background-color: whitesmoke;
}